import { RendererJSAPI } from '../../../../lib';
import { getCSVFromURL, getJSONFromURL } from 'njscore';
import { UI } from './UI';
import { DataProcessTweets } from './DataProcessTweets';

import { FilterGLViewDarkForDarken } from './Tint/FilterGLViewDarkForDarken';

export class Solution extends RendererJSAPI {

    pts: number[][] = [];

    streetOSM: any = [];
    routesMain: any = [];
    routesTreated: any = [];
    routesControl: any = [];
    zones30: any = [];

    ui: UI

    dataProcessTweets: DataProcessTweets;

    constructor(view: any) {
        super(view);

        const canvasGL = document.createElement('canvas');
        canvasGL.style.position = 'absolute';
        canvasGL.style.pointerEvents = 'none';
        canvasGL.style.mixBlendMode = 'overlay';
        canvasGL.width = this.mView.container.clientWidth;
        canvasGL.height = this.mView.container.clientHeight;
        this.mView.container.appendChild(canvasGL);
        const glCtx = canvasGL.getContext('webgl');
        const d = new FilterGLViewDarkForDarken(glCtx, 0.5, 5)
        d.Render(glCtx);




        const canvas = document.createElement('canvas');
        canvas.id = 'layer';
        canvas.width = this.mView.container.clientWidth;
        canvas.height = this.mView.container.clientHeight;
        canvas.style.background = 'rgba(0, 15, 0, 0.4)';
        canvas.style.position = 'absolute';
        canvas.style.pointerEvents = 'none';
        this.mView.container.appendChild(canvas);


        this.ui = new UI(this);
        
        // getJSONFromURL(process.env.PUBLIC_URL + path + 'street_osm.geojson').then((data) => {
        getJSONFromURL('https://raw.githubusercontent.com/NamjuLee/data/master/Paris/street_osm.geojson').then((data) => {
            for (let i = 0; i < data['features'].length; ++i) {
                this.streetOSM.push(data['features'][i]['geometry']['coordinates']);
            }
        });
        getJSONFromURL('https://raw.githubusercontent.com/NamjuLee/data/master/Paris/Paris-main-st.geojson').then((data) => {
            for (let i = 0; i < data['features'].length; ++i) {
                this.routesMain.push(data['features'][i]['geometry']['coordinates']);
            }
        });

        getJSONFromURL('https://raw.githubusercontent.com/NamjuLee/data/master/Paris/treated-st.geojson').then((data) => {
            for (let i = 0; i < data['features'].length; ++i) {
                this.routesTreated.push(data['features'][i]['geometry']['coordinates']);
            }
        });
        getJSONFromURL('https://raw.githubusercontent.com/NamjuLee/data/master/Paris/control-st.geojson').then((data) => {
            for (let i = 0; i < data['features'].length; ++i) {
                this.routesControl.push(data['features'][i]['geometry']['coordinates']);
            }
        });
        getJSONFromURL('https://raw.githubusercontent.com/NamjuLee/data/master/Paris/zones-30.geojson').then((data) => {
            for (let i = 0; i < data['features'].length; ++i) {
                this.zones30.push(data['features'][i]['geometry']['coordinates']);
            }
            //     console.log(this.zones30)
            this.start();
        });

        // const path = '/static/research/slow-zone/';
        // this.dataProcessTweets = new DataProcessTweets(this, process.env.PUBLIC_URL + path + 'tweets_10pct_lat-lon_sample.csv');
        this.dataProcessTweets = new DataProcessTweets(this, 'https://raw.githubusercontent.com/NamjuLee/data/master/Paris/twitter_data_2010-2015.json');

    }
    render(ctx: CanvasRenderingContext2D) {
        ctx.globalCompositeOperation = 'screen'; //'lighten';
        if (this.ui.isActive_streetOSM) {
            renderLine(ctx, this.streetOSM, '#00FFFF', 0.5, this.toScreen);
        }
        if (this.ui.isActive_zones_30) {
            renderMultiPolygon(ctx, this.zones30, '#0000ff39', 0.5, this.toScreen);
        }
        if (this.ui.isActive_Paris_main_st) {
            renderLine(ctx, this.routesMain, '#00FFFF', 0.5, this.toScreen);
        }

        if (this.ui.isActive_control_st) {
            renderLine(ctx, this.routesControl, '#ff0000', 0.5, this.toScreen);
        }
        if (this.ui.isActive_treated_st) {
            renderLine(ctx, this.routesTreated, '#00ff00', 0.5, this.toScreen);
        }

        if (this.ui.isActive_twitter) {
            // for (let i = 0; i < this.pts.length; ++i) {
            //     const projected = this.toScreen(this.pts[i][0], this.pts[i][1]);
            //     renderPoint(ctx, projected[0], projected[1], this.time + 3, 'rgba(255, 0, 0, 0.85)');
            // }

            this.dataProcessTweets.render(ctx);
        }

        // this.isStatic = true;
    }
    keyDown(k: KeyboardEvent): void {
        //     console.log('key pressed', k);
    }
    mouseDown(x: number, y: number): void {
        //     console.log('mouse down')
    }
    mouseUp(x: number, y: number): void {
        //     console.log('mouse up')
    }
    mouseClick(x: number, y: number): void {
        //     console.log(x, y)
        const mapProjected = this.toMap(x, y);
        this.pts.push(mapProjected);
    }
    mouseMove(x: number, y: number) {

    }
}

const renderPoint = (ctx: CanvasRenderingContext2D, x: number, y: number, t: number, color: string) => {
    ctx.fillStyle = color;
    ctx.beginPath();
    ctx.arc(x, y, (Math.sin(t * 0.8) + 4) * 2, 0, Math.PI * 2);
    ctx.closePath();
    ctx.fill();
}

const renderLine = (ctx: CanvasRenderingContext2D, paths: any, color: string, lineWidth: number, toScreen: (x: number, y: number) => number[]) => {
    ctx.strokeStyle = color;
    for (let i = 0; i < paths.length; ++i) {
        const route = paths[i]

        for (let j = 0; j < route.length; ++j) {

            let projected = toScreen(route[j][0][0], route[j][0][1]);
            const path = new Path2D();
            path.moveTo(projected[0], projected[1]);

            for (let k = 1; k < route[j].length; ++k) {
                projected = toScreen(route[j][k][0], route[j][k][1]);
                path.lineTo(projected[0], projected[1]);
            }

            ctx.lineWidth = lineWidth; // Math.sin(lineWidth * 1.5) + 1.15;
            ctx.stroke(path);
        }
    }
};

const renderMultiPolygon = (ctx: CanvasRenderingContext2D, multiPolygons: any, color: string, lineWidth: number, toScreen: (x: number, y: number) => number[]) => {
    // if (multiPolygons === undefined) return;

    ctx.fillStyle = color;
    for (let multi = 0; multi < multiPolygons.length; ++multi) {
        const multiPolygon = multiPolygons[multi];

        for (let polyI = 0; polyI < multiPolygon.length; ++polyI) {
            const polygon = multiPolygon[polyI];

            if (polygon.length === 1) {
                const thePolygon = polygon[0];

                let pl = thePolygon[0];
                // console.log('pl', pl)
                let lon = pl[0];
                let lat = pl[1];
                let projected = toScreen(lon, lat);

                ctx.beginPath();
                ctx.moveTo(projected[0], projected[1]);

                for (let i = 1; i < thePolygon.length; ++i) {

                    pl = thePolygon[i];
                    lon = pl[0];
                    lat = pl[1];
                    projected = toScreen(lon, lat);
                    ctx.lineTo(projected[0], projected[1]);
                }
                ctx.closePath();
                ctx.fill();

            }
        }
    }
};

