
export class TweetData {
    t: number = 0.1;
    color: string = 'rgba(10, 250, 10, 0.8)';
    lon: number;
    lat: number;
    userID: number;
    streetID: number;
    year: number;
    month: number;
    day: number;
    constructor(x: number, y: number) {
        this.lon = x;
        this.lat = y;

    }
    render(ctx: CanvasRenderingContext2D, toScreen: (x: number, y: number)=> number[]) {
        this.t += 0.1;

        ctx.fillStyle = this.color;
        ctx.beginPath();

        const projected = toScreen(this.lon, this.lat);
        ctx.arc(projected[0], projected[1], (Math.sin(this.t * 0.8) + 4) * 2, 0, Math.PI * 2);
        ctx.closePath();

        ctx.fill();
    }
}