import { Solution } from '..';
import { getCSVFromURL, getJSONFromURL } from 'njscore';
import { TweetData } from './TweetData';
export class DataProcessTweets {
    solution: Solution;

    domains;

    tweetDataArray: TweetData[] = [];
    active = false;
    constructor(solution: Solution, path) {
        this.solution = solution;

        this.domains = {
            userID: new Domain('user-id'),
            streetID: new Domain('street-id'),
            year: new Domain('year'),
            month: new Domain('month'),
            day: new Domain('day'),
        }



        getJSONFromURL(path).then((dataRaw) => {
            // const line = d.split(/\r\n|\r|\n/g);
            // console.log(line)
            const key = 'noNeighbsLog';
            let count = 0;
            let latSum = 0;
            let lonSum = 0;

            for (let i = 0; i < dataRaw['cells'].length; ++i) {
                const cell = dataRaw['cells'][i];

                const x = +cell['lon'];
                const y = +cell['lat'];
                // const [x, y] = this.solution.toScreen(+cell['lon'], +cell['lat'])
                // console.log(x, y)
                let keyIndex = +cell[key]
                keyIndex /= 2;
    

                //     console.log(lat, lon, userID, year, month, day, streetID)
                if (x !== 0 && y !== 0) {
                    let tweetData = new TweetData(x, y);
                    this.tweetDataArray.push(tweetData)
                    lonSum += x;
                    latSum += y;
                    count++;
                }
                this.active = true;
            }
            // console.log(lonSum / count, latSum / count)
            this.solution.center(lonSum / count, latSum / count)
            this.solution.zoom(12);
        });



    }
    public render(ctx: CanvasRenderingContext2D) {
        if (!this.active) { return; }
        this.tweetDataArray.map((d: TweetData) => {

            if (d.month === this.solution.ui.month || this.solution.ui.month < 1) {
                d.render(ctx, this.solution.toScreen);
            }
            return 0;
        });
    }
}


class Domain {
    title: string;
    min: number = Number.MAX_VALUE
    max: number = Number.MIN_VALUE
    constructor(titie: string) {
        this.title = titie;
    }
    public compute(value: number) {
        this.min = value < this.min ? value : this.min;
        this.max = value > this.max ? value : this.max;
    }
}
